}

.footer {
	background-color: #1D1D1D;
	&_wrapper {
		padding: 30px 0px 5px 0px;
		border-bottom: 3px solid #D72626;;
	}
	&_menu {
		> ul {
			display: flex;
			justify-content: space-between;
			> li {
				> a {
					color: white;
					&:hover {
