			border-radius: 5px;
			filter: drop-shadow(0px 0px 15px rgba(0, 0, 0, 0.15));
			background-color: #ffffff;
			margin-bottom: 15px;
			&-left {
				position: relative;
				&-btn {
					position: absolute;
					left: 3%;
					top: 80%;
					padding: 7.5px 20.5px;
				}
			}
			&-right {
				display: flex;
				flex-direction: column;
				padding: 23px 15px 0px 16px;
